    import uniLoadMore from "../../components/uni-load-more/uni-load-more.vue"
    export default{
        components: {uniLoadMore},
    	data() {
    		return {
                ismore:false,
                contentText: {
                    contentdown: '查看更多',
                    contentrefresh: '加载中',
                    contentnomore: '没有更多',
                },
                
                serverList:null,
                shareList:null,
                videoList:null,
                productList:null,
    			items: ['服务站', '管家分享','明医说', '健康商品'],
    			current: 0,
                size:10,
                communityPage:1,
                sharePage:1,
                productPage:1,
                videoPage:1,
                mask:false,
                joinBtnText:'',
                touchT:null,
                touchE:null,
                selectItem:null,
                selectIndex:null
    		};
    	},
    	onLoad(options) {
    		this.getMyCommunity()
            this.getMyShare()
            this.getVideoList()
            this.getProductMarkList()
    	},
        onPullDownRefresh() {
            
            if(this.current == 0){
                this.communityPage = 1
                this.getMyCommunity()
            }else if(this.current == 1){
                this.sharePage = 1
                this.getMyShare()
            }else if(this.current == 2){
                this.videoPage = 1
                this.getVideoList()
            }else if(this.current == 3){
                this.productPage = 1
                this.getProductMarkList()
            }
        },
        onReachBottom() {
            console.log('onReachBottom')
            this.status = 'loading'
            uni.showNavigationBarLoading()
            
            if(this.current == 0){
                this.communityPage++
                this.getMyCommunity()
            }else if(this.current == 1){
                this.sharePage++
                this.getMyShare()
            }else if(this.current == 2){
                this.videoPage++
                this.getVideoList()
            }else if(this.current == 3){
                this.productPage++
                this.getProductMarkList()
            }
            
        },
    	filters: {
    		toFixed2: function(value) {
    			return Number(value).toFixed(2);
    		},
    	},
    	methods: {
            clickMask:function(){
                this.mask = !this.mask
            },
            clickJoinBtn:function(){
                if(this.selectItem.join){
                    this.$api.canceljoincommunity({
                        communityId: this.selectItem.id
                    }).then(res=>{
                        console.log(res)
                        if(res.status == 'OK'){
                            uni.showToast({
                                title: "取消加入成功",
                                duration: 2000
                            })
                            this.serverList[this.selectIndex].join = false
                            this.selectIndex = null
                            this.selectItem = null
                            this.mask = false
                        }
                    })
                }else{
                    this.$api.joincommunity({
                        communityId: this.selectItem.id
                    }).then(res=>{
                        console.log(res)
                        if(res.status == 'OK'){
                            uni.showToast({
                                title: "加入成功",
                                duration: 2000
                            })
                            this.serverList[this.selectIndex].join = true
                            this.selectIndex = null
                            this.selectItem = null
                            this.mask = false
                        }
                    })
                }    
            },
            clickMarkBtn:function(){
                if(this.current === 0){
                    this.$api.cancelCommunityMark({
                        data:{
                            communityId:this.selectItem.id
                        }
                    }).then(res=>{
                        console.log(res)
                        if(res.status == 'OK'){
                            this.serverList.splice(this.selectIndex,1)
                            this.selectIndex = null
                            this.selectItem = null
                            this.mask = false
                        }
                    })
                }
                if(this.current === 1){
                    this.$api.marketMaterialCancelMark({
                        id:this.selectItem.id
                    }).then(res=>{
                        console.log(res)
                        if(res.status == 'OK'){
                            this.shareList.splice(this.selectIndex,1)
                            this.selectIndex = null
                            this.selectItem = null
                            this.mask = false
                        }
                    })
                }
                if(this.current === 2){
                    this.$api.videoMarkCancel({
                        videoId: this.selectItem.id
                    }).then(res=>{
                        console.log(res)
                        if(res.status == 'OK'){
                            this.videoList.splice(this.selectIndex,1)
                            this.selectIndex = null
                            this.selectItem = null
                            this.mask = false
                        }
                    })
                }
                if(this.current === 3){
                    this.$api.productMarkDelete({
                    	productId:this.selectItem.id
                    }).then(res=>{
                    	if(res.status=="OK"){
                            this.productList.splice(this.selectIndex,1)
                            this.selectIndex = null
                            this.selectItem = null
                            this.mask = false
                        }
                    })
                }
            },
            touchend:function(){
                this.touchE = new Date().getTime();
            },
            touchstart:function(){
                this.touchT = new Date().getTime();
            },
            longtapServer:function(item,index){
                this.mask = true
                this.joinBtnText = item.join ? '取消加入' : '加入'
                
                this.selectItem = item
                this.selectIndex = index
            },
            
            onClickItem(e) {
            	if (this.current !== e.currentIndex) {
            		this.current = e.currentIndex
            	}
            },
            goDetail({id,type,communityId}){
                if(this.touchE-this.touchT<350){
                    if(type=='VIDEO'){
                        uni.navigateTo({
                            url: `/pages/housekeeper-sharing-video/housekeeper-sharing-video?id=${id}&communityId=${communityId}`,
                        });
                    }else{
                        uni.navigateTo({
                            url: `/pages/housekeeper-sharing-img/housekeeper-sharing-img?id=${id}&communityId=${communityId}`,
                        });
                    }
                }
            },
            goInfo:function(id) {
                if(this.touchE-this.touchT<350){
                    uni.navigateTo({
                    	url: `/pages/liveborad/liveInfo?id=${id}`
                    })
                }
            	
            },
            clickServer:function(index){
                if(this.touchE-this.touchT<350){
                    console.log(this.serverList[index])
                    uni.reLaunch({
                        url:'../index/index?communityid='+this.serverList[index].id
                    })
                }
            },
            clickProduct:function(id,type,communityId){
                if(this.touchE-this.touchT<350){
                    if(communityId){
                    	this.$store.state.communityId = communityId;
                    }
                    uni.navigateTo({
                    	url: `/pages/health-product-detail/health-product-detail?id=${id}&type=${type}`,
                    });
                }
            },
            getMyShare:function(){
                this.$api.findMyShareMark({
                    data:{
                        page:this.sharePage,
                        size:this.size
                    }
                }).then(res=>{
                    console.log(res)
                    if(res.status=="OK"){
                        if(this.sharePage == 1){
                            this.shareList = []
                            if(res.list.length>=this.size){
                                console.log('ismoreismoreismore')
                                this.ismore = true
                            }
                        }
                        
                    	this.shareList = this.shareList.concat(res.list) 
                    }
                    uni.stopPullDownRefresh();
                    uni.hideNavigationBarLoading()
                })
            },
            getVideoList:function(){
                this.$api.getMarkVideoPage({
                    data:{
                        page:this.videoPage,
                        size:this.size
                    }
                }).then(res=>{
                    if(res.status == 'OK'){
                        if(this.videoPage == 1){
                            this.videoList = []
                            if(res.list.length>=this.size){
                                this.ismore = true
                            }
                        }
                        this.videoList = this.videoList.concat(res.list) 
                    }
                    uni.stopPullDownRefresh();
                    uni.hideNavigationBarLoading()
                })
            },
            getProductMarkList:function(){
                this.$api.productMarkList({
                    data:{
                        page:this.productPage,
                        size:this.size
                    }
                }).then(res=>{
                    console.log(res)
                    if(res.status == 'OK'){
                        if(this.productPage == 1){
                            this.productList = []
                            if(res.list.length>=this.size){
                                console.log('ismoreismoreismore')
                                this.ismore = true
                            }
                        }
                        res.list.forEach(item=>{
                            if(item.communityProduct.icon && JSON.parse(item.communityProduct.icon).length>0){
                               item.communityProduct.pic = JSON.parse(item.communityProduct.icon)[0].url
                            }
                            this.productList.push(item);
                        })
                        
                    }
                    uni.stopPullDownRefresh();
                    uni.hideNavigationBarLoading()
                })
            },
            getMyCommunity:function(){
                this.$api.findMyCommunityMark({
                    data:{
                        page:this.communityPage,
                        size:this.size
                    }
                }).then(res=>{
                    console.log(res)
                    if(res.status == 'OK'){
                        if(this.communityPage == 1){
                            this.serverList = []
                            if(res.list.length>=this.size){
                                console.log('ismoreismoreismore')
                                this.ismore = true
                            }
                        }
                        res.list.map(item=>{
                        	var update = false;
                        	if(item.province){
                        	    item.addr = item.province.substring(0, item.province.length - 1) 
                        		update = true
                        	}
                        	if(item.city){
                        	    item.addr = item.addr  + ' | ' + item.city.substring(0,item.city.length - 1)
                        		update = true
                        	}
                        	if(!update){
                        		item.addr = ''
                        	}
							if(item.icon && JSON.parse(item.icon ).length > 0){
							    item.icon = JSON.parse(item.icon)[0].url
							}
                            this.serverList .push(item) 
                        })
                        
                    }
                    uni.stopPullDownRefresh();
                    uni.hideNavigationBarLoading()
                })
            },
			praiseAdd(id, index) {
				this.$api.praiseAdd({
					videoId: id
				}).then(res=>{
					// this.getMyhvideo()
					this.videoList[index].praiseStatus = !this.videoList[index].praiseStatus
				})
			},
			praiseCancel(id, index) {
				this.$api.praiseCancel({
					videoId: id
				}).then(res=>{
					// this.getMyhvideo()
					this.videoList[index].praiseStatus = !this.videoList[index].praiseStatus
				})
			},
			videoMarkCancel(id, index) {
				this.$api.videoMarkCancel({
					videoId: id
				}).then(res=>{
					this.videoPage = 1
					this.getVideoList()
				})
			}
        },
    }
